
import { defineComponent, onMounted } from 'vue';
import PostService from '../services/PostService';

export default defineComponent({
  name: 'PostsView.vue',
  setup() {
    const service = new PostService()
    const posts = service.getPosts()

    onMounted(async () => {
      await service.fetchAll()
    })

    return {posts}
  }
});
